.app-wrapper {
    display: grid;
    width: 980px;
    min-height: 100vh;
    margin: 0 auto;
    grid-template-columns: 2fr 10fr;
    grid-template-rows: 60px 1fr;
    grid-gap: 10px;
    grid-template-areas:
    'h h h'
    'n c c';
}

.app-wrapper-content {
    grid-area: c;
    background-color: whitesmoke;
}

.clearfix::after {
    content: '';
    display: block;
    clear: both;
}

